<template>
	<q-card
		flat
		bordered
		class="bg-grey-9 q-pb-xl text-white my-card full-height"
	>
		<q-card-section>
			<div v-if="!updating" class="text-h6">{{ post.title }}</div>
			<q-input
				v-else
				outlined
				dense
				v-model="title"
				dark
				class="q-mb-md"
			/>
			<div class="text-subtitle2 text-primary">by {{ post.author }}</div>
		</q-card-section>

		<q-separator />

		<q-card-section>
			<small
				>{{ getDate(post.created_at) }}
				<q-tooltip content-class="bg-indigo" :offset="[10, 10]">
					Created date
				</q-tooltip>
			</small>
			<small
				class="q-ml-sm text-primary"
				v-if="post.updated_at.toString() !== 'Invalid Date'"
				>{{ getDate(post.updated_at) }}
				<q-tooltip content-class="bg-purple" :offset="[10, 10]">
					Last updated date
				</q-tooltip>
			</small>
		</q-card-section>

		<q-card-section class="q-pt-none">
			<div v-if="!updating">
				{{ post.text }}
			</div>
			<q-input
				v-else
				outlined
				dense
				v-model="text"
				dark
				class="q-mb-md"
			/>
		</q-card-section>

		<q-card-actions class="bg-grey-10 q-pa-sm justify-end absolute-bottom">
			<slot></slot>
			<q-icon
				v-if="updating === false"
				@click="startEditing()"
				size="sm"
				name="far fa-edit"
				class="text-primary cursor-pointer"
			/>
			<q-btn
				color="purple"
				size="sm"
				unelevated
				dense
				@click="updatePost(post._id)"
				v-if="updating"
				>Edit</q-btn
			>
		</q-card-actions>
	</q-card>
</template>

<script>
import PostService from "@/PostService";
export default {
	props: {
		post: {
			type: Object,
		},
	},
	data() {
		return {
			updating: false,
			title: this.post.title,
			text: this.post.text,
		};
	},
	methods: {
		getDate(date) {
			return `${date.getDate()}/${date.getMonth()}/${date.getFullYear()}`;
		},

		startEditing() {
			this.updating = true;
		},

		async updatePost(id) {
			if (
				this.post.title === this.title &&
				this.post.text === this.text
			) {
				this.updating = false;
				return;
			}
			this.startLoading("Updating article...");
			await PostService.updatePost(id, this.title, this.text);
			let posts = await PostService.getPosts();
			this.$emit("fetchPosts", posts);
			console.log(posts);
			this.updating = false;
			this.finishLoading();
		},

		startLoading(string) {
			this.$q.loading.show({
				message: string,
			});
		},
		finishLoading() {
			this.$q.loading.hide();
		},
	},
};
</script>

<style scoped></style>
